<template>
  <div>

    <el-breadcrumb separator-class="el-icon-arrow-right" class="el-breadcrumb">
      <el-breadcrumb-item :to="{path:'/main'}">首页</el-breadcrumb-item>
      <el-breadcrumb-item>住户信息管理</el-breadcrumb-item>
      <el-breadcrumb-item>住户管理</el-breadcrumb-item>
    </el-breadcrumb>

    <el-card class="el-card">

      <el-row :gutter="10">
        <el-col :span="8">
          <el-input placeholder="请输入姓名" v-model="customerName">
            <el-button slot="append" icon="el-icon-search" @click="handleSearch"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible=true">添加住户</el-button>
        </el-col>
      </el-row>

      <el-table :data="tableData">
        <el-table-column prop="customerId" label="序号" width="70"></el-table-column>
        <el-table-column prop="customerName" label="住户姓名" width="120"></el-table-column>
        <el-table-column prop="customerSex" label="住户性别" width="120"></el-table-column>
        <el-table-column prop="customerPhone" label="住户电话" width="120"></el-table-column>
        <el-table-column prop="customerAddress" label="住户地址" width="120"></el-table-column>
        <el-table-column prop="customerTime" label="入住时间" width="120"></el-table-column>
        <el-table-column label="操作" min-width="170">
          <template slot-scope="scope">
            <el-button type="primary" @click="showReadDialog(scope.row.customerId)" icon="el-icon-reading">查看</el-button>
            <el-button type="warning" @click="showEditDialog(scope.row.customerId)" icon="el-icon-edit">修改</el-button>
            <el-button type="danger" icon="el-icon-delete" @click="removeCustomerById(scope.row.customerId)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>

      <el-pagination
        background
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-size="pageSize"
        layout="total,prev, pager, next"
        :total="total">
      </el-pagination>
    </el-card>

    <el-dialog
      title="添加住户"
      :visible.sync="addDialogVisible"
      width="40%"
    >
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="90px" @close="addDialogClosed">
        <el-form-item label="姓名" prop="customerName">
          <el-input v-model="addForm.customerName"></el-input>
        </el-form-item>
        <el-form-item label="性别" prop="customerSex">
          <el-input v-model="addForm.customerSex"></el-input>
        </el-form-item>
        <el-form-item label="电话" prop="customerPhone">
          <el-input v-model="addForm.customerPhone"></el-input>
        </el-form-item>
        <el-form-item label="住址" prop="customerAddress">
          <el-input v-model="addForm.customerAddress"></el-input>
        </el-form-item>
        <el-form-item label="入住时间" prop="customerTime">
          <el-input v-model="addForm.customerTime"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addCustomer">确 定</el-button>
      </span>
    </el-dialog>

    <el-dialog
      title="修改住户信息"
      :visible.sync="editDialogVisible"
      width="40%"
    >
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="90px" @close="editDialogClosed">
        <el-form-item label="姓名" prop="customerName">
          <el-input v-model="editForm.customerName"></el-input>
        </el-form-item>
        <el-form-item label="性别" prop="customerSex">
          <el-input v-model="editForm.customerSex"></el-input>
        </el-form-item>
        <el-form-item label="电话" prop="customerPhone">
          <el-input v-model="editForm.customerPhone"></el-input>
        </el-form-item>
        <el-form-item label="住址" prop="customerAddress">
          <el-input v-model="editForm.customerAddress"></el-input>
        </el-form-item>
        <el-form-item label="入住时间" prop="customerTime">
          <el-input v-model="editForm.customerTime"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editCustomer">确 定</el-button>
      </span>
    </el-dialog>

    <el-dialog
      title="查看"
      :visible.sync="readDialogVisible"
      width="40%"
    >
      <el-form :model="readForm"  ref="editFormRef" label-width="90px" >
        <el-form-item label="姓名" prop="customerName">
          <el-input v-model="readForm.customerName" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="性别" prop="customerSex">
          <el-input v-model="readForm.customerSex" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="电话" prop="customerPhone">
          <el-input v-model="readForm.customerPhone" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="住址" prop="customerAddress">
          <el-input v-model="readForm.customerAddress" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="入住时间" prop="customerTime">
          <el-input v-model="readForm.customerTime" :disabled="true"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="readDialogVisible = false">关  闭</el-button>
      </span>
    </el-dialog>


  </div>
</template>

<script>
    export default {
      name: "customerManage",
      data() {
        return {
          pageSize: 8,
          currentPage: 1,
          tableData: {
            customerId:"",
            customerName:"",
            customerSex:"",
            customerPhone:"",
            customerAddress:"",
            customerTime:""
          },
          total: 0,
          customerName: "",
          addDialogVisible: false,
          addForm:{
            customerName:"",
            customerSex:"",
            customerPhone:"",
            customerAddress:"",
            customerTime:""
          },
          addFormRules:{
            customerName:[
              {required: true, message: '姓名不可为空', trigger: 'blur'},
              {min:1,max:20,message: '姓名长度在2~20个字符之间',trigger:'blur'}
            ],
            customerSex:[
              {required: true, message: '性别不可为空', trigger: 'blur'}
            ],
            customerPhone:[
              {required: true, message: '手机号不可为空', trigger: 'blur'}
            ],
            customerAddress:[
              {required: true, message: '地址不可为空', trigger: 'blur'}
            ],
            customerTime:[
              {required: true, message: '入住时间不可为空', trigger: 'blur'}
            ]
          },
          editDialogVisible:false,
          editForm:{
            customerId:"",
            customerName:"",
            customerSex:"",
            customerPhone:"",
            customerAddress:"",
            customerTime:""
          },
          editFormRules:{
            customerName:[
              {required: true, message: '姓名不可为空', trigger: 'blur'},
              {min:1,max:20,message: '姓名长度在2~20个字符之间',trigger:'blur'}
            ],
            customerSex:[
              {required: true, message: '性别不可为空', trigger: 'blur'}
            ],
            customerPhone:[
              {required: true, message: '手机号不可为空', trigger: 'blur'}
            ],
            customerAddress:[
              {required: true, message: '地址不可为空', trigger: 'blur'}
            ],
            customerTime:[
              {required: true, message: '入住时间不可为空', trigger: 'blur'}
            ]
          },
          readDialogVisible:false,
          readForm:{
            customerId:"",
            customerName:"",
            customerSex:"",
            customerPhone:"",
            customerAddress:"",
            customerTime:""
          }
        }
      },
      created() {
        this.queryCustomerList()
      },
      methods: {
        queryCustomerList() {
          this.$http.get('customer/queryCustomer',
            {params: {currentPage: this.currentPage, pageSize: this.pageSize, customerName: this.customerName}})
            .then(res => {
              this.total = res.data.total;
              this.tableData = res.data.data;
            });
        },
        handleCurrentChange(currentPage) {
          this.currentPage = currentPage
          this.queryCustomerList()
        },
        handleSearch() {
          this.currentPage = 1
          this.queryCustomerList()
        },
        addDialogClosed(){
          this.$refs.addFormRef.resetFields()
        },
        addCustomer(){
          this.$refs.addFormRef.validate(async valid=>{
            if(!valid) return
            const {data:res}=await this.$http.post("customer/addCustomer",this.addForm)
            if(res.code!==200){
              this.$message.error("添加用户信息失败,请检查用户信息")
            }else {
              this.$message.success("添加用户信息成功")
              this.addDialogVisible=false
              this.queryCustomerList()
            }
          })
        },
        showEditDialog(customerId){
          this.editDialogVisible =true
          this.$http.get('customer/getCustomerById',{params:{customerId:customerId}})
            .then(res => {
              this.editForm = res.data;
            });
        },
        editDialogClosed(){
          this.$refs.editFormRef.resetFields()
        },
        editCustomer(){
          this.$refs.editFormRef.validate(async valid =>{
            if(!valid) return
            const {data:res}=await this.$http.post("customer/editCustomer",this.editForm)
            if(res.code!==200){
              this.$message.error("修改用户信息失败,请检查用户信息")
            }else {
              this.$message.success("修改用户信息成功")
              this.editDialogVisible=false
              this.queryCustomerList()
            }
          })
        },
        removeCustomerById(customerId){
          this.$confirm('此操作将永久删除, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            this.$http.get('customer/removeCustomerById',{params:{customerId:customerId}});
            this.$message({
              type: 'success',
              message: '删除成功!'
            });
            this.queryCustomerList();
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });
          });
        },
        showReadDialog(customerId){
          this.readDialogVisible =true
          this.$http.get('customer/getCustomerById',{params:{customerId:customerId}})
            .then(res => {
              this.readForm = res.data;
            });
        }
      }
    }
</script>

<style scoped lang="scss">
  .el-breadcrumb {
    margin-bottom: 15px;
    font-size: 18px;
  }

  .el-card {
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15);
  }

  input:disabled{
    color:#333333;
    background: white;
    opacity: 1;
    -webkit-text-fill-color: #333333
  }
</style>



